import React from 'react';

function Services() {
  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-4xl font-bold mb-8">Nuestros Servicios Legales</h1>

      <section className="grid grid-cols-1 md:grid-cols-2 gap-8">
        <article className="bg-white shadow-lg rounded-lg p-6">
          <h2 className="text-2xl font-semibold mb-4">Derecho Corporativo y Comercial</h2>
          <ul className="list-disc list-inside space-y-2">
            <li>Constitución y estructuración de empresas</li>
            <li>Fusiones y adquisiciones</li>
            <li>Contratos comerciales y negociaciones</li>
            <li>Gobierno corporativo y cumplimiento normativo</li>
            <li>Asesoría en comercio internacional</li>
          </ul>
        </article>

        <article className="bg-white shadow-lg rounded-lg p-6">
          <h2 className="text-2xl font-semibold mb-4">Propiedad Intelectual y Tecnología</h2>
          <ul className="list-disc list-inside space-y-2">
            <li>Registro y protección de marcas y patentes</li>
            <li>Litigios de propiedad intelectual</li>
            <li>Contratos de licencia y transferencia de tecnología</li>
            <li>Protección de datos y privacidad</li>
            <li>Asesoría legal en e-commerce y startups tecnológicas</li>
          </ul>
        </article>

        <article className="bg-white shadow-lg rounded-lg p-6">
          <h2 className="text-2xl font-semibold mb-4">Derecho Laboral</h2>
          <ul className="list-disc list-inside space-y-2">
            <li>Contratos de trabajo y políticas laborales</li>
            <li>Negociación colectiva y relaciones sindicales</li>
            <li>Litigios laborales y resolución de conflictos</li>
            <li>Asesoría en seguridad social y beneficios</li>
            <li>Cumplimiento de normativas laborales</li>
          </ul>
        </article>

        <article className="bg-white shadow-lg rounded-lg p-6">
          <h2 className="text-2xl font-semibold mb-4">Derecho Ambiental y Sostenibilidad</h2>
          <ul className="list-disc list-inside space-y-2">
            <li>Cumplimiento de regulaciones ambientales</li>
            <li>Litigios y mediación en conflictos ambientales</li>
            <li>Asesoría en proyectos de energía renovable</li>
            <li>Evaluación de impacto ambiental</li>
            <li>Estrategias de sostenibilidad corporativa</li>
          </ul>
        </article>
      </section>

      <div className="mt-12 text-center">
        <p className="text-xl mb-4">¿Necesita asesoría legal en un área específica?</p>
        <a href="/contacto" className="bg-blue-600 text-white px-6 py-2 rounded hover:bg-blue-700 transition duration-300">
          Contáctenos para una Consulta Personalizada
        </a>
      </div>
    </div>
  );
}

export default Services;
